  align-items: center;
  box-sizing: border-box;
  margin-bottom: 12px;
  position: relative;
  overflow: hidden;
  transition: 0.3s ease;
}

.menu__item:last-child {
  margin-bottom: 0;
}

.menu__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-right: 15px;
  box-sizing: border-box;
}

.menu__text {
  width: 287px;
  gap: 0.3125rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 15px;
  box-sizing: border-box;
}

.menu__price {
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  margin-right: 15px;
  box-sizing: border-box;
  align-self: flex-end;
}

/* CHECKBOX & ANIMATION */
.menu__check {
  width: 0;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #99E2D0;
  border-radius: 0 15px 15px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 1;
  transition: width 0.3s ease;
}

.menu__check-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #99E2D0;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.menu__check .fa-check {
  font-size: 12px;
  opacity: 0;
  color: #99E2D0;
  transition: opacity 0.3s ease;
}

.menu__check-input {
  display: none;
}

/* HOVER & CHECKED STATES */
.menu__item:hover {
  transform: translateX(-5px);
}

.menu__item:hover .menu__content {
  border-right: 5px solid #99E2D0;
}

.menu__item:hover .menu__check,
.menu__check-input:checked + .menu__check {
  width: 59px;
}

.menu__item:hover .menu__price,
.menu__check-input:checked + .menu__check + .menu__content .menu__price {
  transform: translateX(-59px);
}

.menu__item:hover .menu__check .fa-check,
.menu__item:hover .menu__check .menu__check-circle,
.menu__check-input:checked + .menu__check .fa-check,
.menu__check-input:checked + .menu__check .menu__check-circle {
  opacity: 1;
}

/* ANIMATIONS */
@keyframes simpleFade {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
/* LOADER */
.loader-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1000;
  animation: fadeOut 1s ease-out 3s forwards;
}

.loader {
  width: 60px;
  height: 60px;
  border: 6px solid transparent;
  border-top: 6px solid #9356DC;
  border-right: 6px solid #FF79DA;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loader-text {
  margin-top: 15px;
  font-size: 1.5rem;
  font-family: "Shrikhand", sans-serif;
  color: #000000;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: hidden;
  }
}
/* SECTION MENU */
.menu__section {
  margin-top: 2.625rem;
  padding: 13px;
}

.menu__section h2 {
  margin: 0;
  position: relative;
  margin-bottom: 1.5625rem;
}
.menu__section h2::after {
  content: "";
  width: 50%;
  width: 1.5rem;
  border-top: 3px solid #99E2D0;
  position: relative;
  left: 0;
  top: 5px;
  display: block;
}

.menu__section {
  margin-top: 2.625rem;
  padding: 13px;
}
.menu__section h2 {
  position: relative;
  margin-bottom: 1.5625rem;
  padding-left: 1.0625rem;
}

.menu__item {
  width: 100%;
  padding: 12px 0;
  border-radius: 15px;
  box-shadow: 0px 0.25rem 0.9375rem rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  margin-bottom: 12px;
  /* Ajout du gap entre les bo√Ætes */
}

.menu__item:last-child {
  margin-bottom: 0;
  /* Supprime la marge du dernier √©l√©ment */
}

.menu__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-right: 15px;
  box-sizing: border-box;
}

.menu__text {
  width: 287px;
  gap: 0.3125rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 15px;
  box-sizing: border-box;
}

.menu__title {
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 21.09px;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
