}

.menu__description {
  font-family: "Roboto", sans-serif;
  font-size: 0.94rem;
  font-weight: 300;
  line-height: 1.1rem;
  text-align: left;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
