  margin: 0;
}

.menu__price {
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  font-weight: 500;
  margin-right: 15px;
  box-sizing: border-box;
  align-self: flex-end;
}

.order {
  /* Hauteur fixe de 187px pour laisser un espace d√©fini entre le menu et le footer */
  height: 187px;
  /* On centre le bouton horizontalement et verticalement */
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn--order {
  background: linear-gradient(193.33deg, #9356DC -11.44%, #FF79DA 123.93%);
  border-radius: 25px;
  box-shadow: 0px 0.25rem 0.625rem rgba(0, 0, 0, 0.25);
  color: #FFFFFF;
  font-family: "Roboto", sans-serif;
  font-size: 1.125rem;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
  padding: 13px 1rem;
  transition: background 0.3s ease, box-shadow 0.3s ease;
  /* Taille du bouton */
  width: 13.625rem;
  height: 3.125rem;
  /* Au survol, on utilise un autre gradient + une ombre diff√©rente */
}
.btn--order:hover {
  /* D√©finis un gradient "un peu plus clair" √† la main */
  background: linear-gradient(193.33deg, #9356DC -11.44%, #FF79DA 123.93%);
  /* Ombre plus marqu√©e */
  box-shadow: 0px 0.25rem 0.625rem rgba(0, 0, 0, 0.25);
}

