/* ==================== STEPS ==================== */
/* ‚úÖ Styles de base (Mobile - par d√©faut, < 768px) */
.steps {
  width: 100%;
  padding: 0 1.25rem;
  /* Padding horizontal pour le contenu int√©rieur */
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* Centrage horizontal du contenu en mobile */
  justify-content: center;
  gap: 20px;
  /* Espacement vertical entre titre et container */
  margin-bottom: 40px;
  /* Marge basse pour s√©parer la section des suivantes en mobile */
}

.steps__title {
  width: 100%;
  margin-top: 48px;
  /* Marge haute pour espacer le titre du haut de la section */
  font-family: "Arial", sans-serif;
  color: #000000;
}

.steps__container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
  /* Espacement vertical entre les steps en mobile */
  justify-items: center;
  /* Centrage horizontal des steps en mobile */
  width: 100%;
}

.steps__list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  /* Espacement vertical entre les steps dans la liste en mobile */
  width: 100%;
}

.step {
  width: 100%;
  position: relative;
  background-color: #FFFFFF;
  border-radius: 1.25rem;
  box-shadow: 0px 0.25rem 0.9375rem rgba(0, 0, 0, 0.1);
  padding: 20px;
  /* Padding int√©rieur de chaque step */
  min-height: 72px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 0;
}

.step__number,
.step__icon,
.step__icon--first,
.step__text,
.step__icon__store,
.steps__list .step:nth-child(1) .step__icon {
  /* Styles des √©l√©ments internes inchang√©s */
}

/* ‚úÖ Version Tablette et Desktop (>= 768px) */
@media screen and (min-width: 768px) {
  .steps {
    max-width: 1440px;
    height: 228.379px;
    width: 100%;
    margin: 0 auto;
    /* Centrage horizontal de la section */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* Alignement √† gauche du contenu */
    padding: 50px 180px;
    /* Padding haut/bas et gauche/droite pour desktop */
    gap: 25px;
    /* Espacement vertical entre titre et container */
    margin-bottom: 0;
    /* Suppression de la marge bottom mobile pour desktop */
  }
  .steps__title {
    width: 100%;
    margin-top: 0;
    /* Suppression du margin top mobile car padding sur .steps */
    margin-bottom: 0;
    text-align: left;
    /* Alignement du texte du titre √† gauche */
  }
  .steps__container {
    max-width: 100%;
    margin: 0 auto;
    width: auto;
  }
  .steps__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
